@import 'stylesheets/abstracts/variables';

.header {
  align-items: center;
  background-color: $primaryColor;
  color: $secondaryColor;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 40px;

  &__h1 {
    font-size: 1.4rem;
    font-weight: bold;
    width: 100%;
  }

  &__h2 {
    display: flex;
    font-size: 0.8rem;
    flex-direction: column;
    width: 100%;
  }
}

