@import 'stylesheets/abstracts/variables';


.App {
  line-height: 1.5;
  text-align: center;
}

.main {
  margin: 10px auto 30px;

  &__loading {
    animation: 3s linear 1s infinite alternate slidein;
    font-size: 1.5rem;
    left: 25%;
    position: absolute;
    top: 50%;
  }
}

@keyframes slidein {
  from { transform: scaleX(0); }
  to   { transform: scaleX(1); }
}

.footer {
  background-color: $primaryColor;
  bottom: 0;
  color: $colorGrey9;
  font-size: 0.8rem;
  min-height: 15px;
  position: fixed;
  width: 100%;

  &__optional-text {
    display: none;
  }
}

@media only screen and (min-width: map-get($grid-breakpoints, sm)) {
  .footer__optional-text {
    display: inline;
  }
}
