@import 'stylesheets/abstracts/variables';

.country-filter-form {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 1fr;
  grid-template-areas:
    "search-box"
    "sort-options";
  margin: 0 5px 5px;

  &__input {
    border-radius: 15px;
    color: $fontColor;
    font-size: 1.3rem;
    grid-area: search-box;
    height: 35px;
  }

  &__sort-options-label {
    font-size: 1.2rem;
    grid-area: sort-options;
  }

  &__sort-options {
    border-radius: 15px;
    color: $fontColor;
    font-size: 1.2rem;
    height: 35px;
  }
}

@media only screen and (min-width: map-get($grid-breakpoints, sm)) {
  .country-filter-form {
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "search-box sort-options";
  }
}

