@import 'stylesheets/abstracts/variables';

.country-table {
  border: 2px solid $lightGrey2;
  &__caption {
    background-color: $secondaryColor;
    color: $primaryColor;
    font-size: 1.3rem;
    font-weight: bold;
  }

  &__th {
    background-color: $secondaryColor;
    border: 1px solid $lightGrey1;
    position: sticky;
    top: -2px;
    box-shadow: 0 2px 2px -1px rgba(0, 0, 0, 0.4);
  }

  &__row:nth-of-type(even) {
    background-color: $secondaryColor;
  }

  &__cell {
    border: 1px solid $lightGrey1;
  }
}

@media only screen and (max-width: map-get($grid-breakpoints, sm)) {
  [class^="country-table__"] {
    display: block;
  }

  .country-table {
    width: 100%;
    &__th {
      position: absolute;
      top: -9999px;
    }

    &__row {
      margin: 0 0 1rem 0;
    }

    &__cell {
			position: relative;
			padding-left: 50%;
      &:before {
        position: absolute;
        left: 6px;
      }
      &:nth-of-type(1):before { content: "Country" }
      &:nth-of-type(2):before { content: "Cumulative cases" }
      &:nth-of-type(3):before { content: "Cumulative deaths" }
      &:nth-of-type(4):before { content: "New cases" }
      &:nth-of-type(5):before { content: "New deaths" }
      &:nth-of-type(6):before { content: "Percent cum.deaths / cases" }
    }
  }
}

