// -----------------------------------------------------------------------------
// This file contains all application-wide Sass variables.
// -----------------------------------------------------------------------------

// colors
$primaryColor: #0093D5;
$secondaryColor: #eee;
$fontColor: #474747;
$colorGrey0: #333333;
$colorGrey9: #fbfbfb;
$lightGrey1: #ddd;
$lightGrey2: #ccc;

// Screen sizes

$grid-breakpoints: (
  xs: 0,
  sm: 640px,
  md: 768px,
  lg: 992px,
  xl: 1320px
);

$container-max-widths: (
  sm: 604px,
  md: 720px,
  lg: 960px,
  xl: 1260px
);

// Z-index values as per https://confluence.verivox.ads/display/TUX/Z-Index+Definition
$zIndexOverlay: 900
